body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.media {
    height: 150px;
    object-fit: contain;
}

.card {
    max-width: 200px;
    margin-bottom: 10px;
    margin-left: auto;
    margin-right: auto;
}
.green {
    box-shadow: 0 4px 8px 0 rgba(5, 247, 37, 0.2), 0 6px 20px 0 rgba(8, 226, 44, 0.19) !important;
    -webkit-transition: ease-in 300ms;
    transition: ease-in 300ms;
}

.red {
    box-shadow: 0 4px 8px 0 rgba(247, 5, 5, 0.2), 0 6px 20px 0 rgba(226, 8, 8, 0.19) !important;
    -webkit-transition: ease-in 300ms;
    transition: ease-in 300ms;
}


